@font-face {
  font-family: 'icomoon';
  src:  url('icomoon.eot?5r3wbk');
  src:  url('icomoon.eot?5r3wbk#iefix') format('embedded-opentype'),
    url('icomoon.ttf?5r3wbk') format('truetype'),
    url('icomoon.woff?5r3wbk') format('woff'),
    url('icomoon.svg?5r3wbk#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-check:before {
  content: "\e900";
}
.icon-clear:before {
  content: "\e901";
}
.icon-clipboard:before {
  content: "\e902";
}
.icon-instruction:before {
  content: "\e903";
}
.icon-key:before {
  content: "\e904";
}
.icon-lock:before {
  content: "\e905";
}
.icon-unlock:before {
  content: "\e906";
}
